// ***** big page sections like .header, .nav, .footer, etc. *****

/**
* Container for top language drop down and search form
*/

.lang-seach{
    padding: 25px 0;
    
    text-align: right;
    
    display: flex;
    align-content: center;
    align-items: center;
    justify-content: flex-end;
    
}// lang-seach


/**
*  Order tickets button on top
*/

.tickets{
    float: right;
    display: inline-block;
}


/**
* Container for site navigation
*/

.menu{    
    display: flex;
    align-items: center;
    justify-content: space-between;
}// menu


/**
* Display inline navigation links
*/

.main-nav{
    display: inline-block;
}// main-nav


/**
* Top space for beatween navigation and tiles
*/
.slide{    
    &__container{
        padding-top: 375px;
    }
}// slide




/**
* Positioning slide control arrows 
*/
.slide{ 
    .arrow{
        top: 220px;
    }
}//

/**
* Positioning news feed arows
*/
.news{ 
    .arrow{
        bottom: 35px;
    }
}//


/**
* Positioning button to right
*/
.lecture{
    .button:last-of-type{
        float: right;
    }
    
    &--inner{
         .button:last-of-type{
           float: none;
        }
    }
}

/**
* Additional purple background for ellements
*/
.references{
    position: relative;
    
    &:after{
        content: '';
        display: block;
        width: 100%;
        height: 200px;
        position: absolute;
        bottom: 0;
        right: 0;
        background: $purple;
        z-index: -1;
    }
}// end 


.footer{
    .subscribe{
        margin-bottom: 60px;
    }
}


