@import 'reset';
@import 'fonts';

body{
    font-family: 'Roboto', sans-serif;
    font-size: 16px;
}


/**
* Main container
*/
[class$="__container"]{
    
    width: 100%;
    max-width: $desktop--xl;
    margin: 0 auto;
    position: relative;
    padding-left: 20px;
    padding-right: 20px;
        
    &:before{
        content: '';
        display: table;
    }
    
    &:after{
        content: '';
        display: table;
        clear: both;
    }
}

/**
* Fluid container
*/
[class^="container--fluid"]{    
    max-width: 1280px;
}

/**
* Row class
*/
[class$="--row"]{
    margin: 0 -20px;
}


/**
* Full width blocks
*/
.block{
    width: 100%;    
    float: left;
}

body{
    min-width: $desktop--xl;
}