// ***** re-usable modules *****


/**
* Inner header margin bottom
*/
.header--inner{
    margin-bottom: 43px;
}

/**
* top search form
*/

.search{
    display: inline-block;
    
    &__input{
        width: auto;
        min-height: 15px;
        padding: 0 5px 5px 0;
        border: none;
        border-bottom: 1px solid $black;
        margin: 0 80px 0 15px;
        
        font-size: em(15);
        
        background: none;
        
        outline: none;
        appearance: none;
        
    }
    
    &__button{
        width: 15px;
        height: 14px;
        border: 0;
        margin: 0;
        padding: 5px;
        
        appearance: none;
        
        background: url(../img/sprite/m-glass.png#controls);
        background-repeat: no-repeat;
        background-color: transparent;
        
        outline: none;
        cursor: pointer;
    }
    
}


/**
* Subscribe form
*/
.subscribe{
    width: 100%;
    float: left;
    display: block;
    
    &__input{
        width: 250px;
        float: left;
        min-height: 15px;
        padding: 0 15px 5px 0;
        border: none;
        border-bottom: 1px solid $white;
        margin: 0;
        
        font-size: em(15);
        color: $white;
        
        background: none;
        
        outline: none;
        appearance: none;
        
    }
    
    &__button{
        width: 13px;
        height: 12px;
        border: 0;
        margin: 0;
        padding: 5px;
        position: relative;
        left: -13px;
        
        appearance: none;
        
        background: url(../img/sprite/send.png#controls);
        background-repeat: no-repeat;
        background-color: transparent;
        
        outline: none;
        cursor: pointer;
    }
}


/**
* language drop-down
*/

.lang{
    display: inline-block;
    padding: 0 20px 0 30px;
    
    &__current{
        position: relative;
        
        text-transform: uppercase;
        font-size: em(15);
        color: $gray;
        text-align: center;
        line-height: 1;
        
        cursor: pointer;
        
        &:after{
            content: '';
            display: block;
            width: 11px;
            height: 6px;
            
            background: url(../img/sprite/carret.png#controls);
            
            position: absolute;
            top: 25%;
            right: -20px;
        }
    }
    
    &__drop-down{
        height: 0;
        overflow: hidden;
    }
    
}// .lang




/**
*  site logo and slug
*/

.logo{
    display: inline-block;
    text-decoration: none;
    color: $black;
    float: left;    
    
    display: flex;   
    align-items: center;
    
    &__ico{
        display: block;
        width: 103px;
        height: 103px;
        background: url(../img/logo.png) no-repeat transparent;
        background-size: cover;
        float: left;
        
        
    }
    
    &__txt{
        display: block;
        float: left;
        padding-left: 20px;
    }
    
    &__name{        
        display: block;  
        margin-bottom: 5px;
        
        font-size: em(23);
        font-weight: bold;        
    }
    
    &__slug{
        display: block;        
        font-size: em(11); 
    }
    
    &--bottom{
        color: $white;
        margin-bottom: 60px;
        
        .logo__name{
            font-size: em(17);
        }
    }
    
}// .logo




/**
* navigation links
*/

.top-nav{
    float: left;
    position: relative;
    
    &__line{
        display: block;
        width: 100%;
        height: 3px;
        background: $white;
        position: absolute;
        left: 0;
        bottom: 0;
    }
    
    &__marquee{
        display: block;
        height: 3px;
        background: $purple;
        position: absolute;
        bottom: 0;
        width: 0;
        left: 0;
        
        transition: all .3s ease;
    }
    
    &__item{
        display: inline-block;
        float: left;
    }
    
    > .top-nav__item:first-child .top-nav__link{
        padding-left: 20px;
    }
    
    > .top-nav__item:last-of-type .top-nav__link{
        padding-right: 20px;
    }
    
    &__link{
        display: block;
        padding: 20px 10px;
        
        color: $black;
        text-decoration: none;
        text-transform: uppercase;
        text-align: center;
        font-size: em(14);
        font-weight: 600;
        
        transition: all .2s ease;
        
        background: $white;
        
        &:hover, &:focus{
            color: $purple;
        }
    }
    
}// top-nav





/**
* button styles 
*/

.button{
    display: inline-block;
    padding: 17px 30px;
    border: 3px solid transparent; 
    
    text-align: center;
    text-transform: uppercase;
    line-height: 1;
    font-size: em(14);
    font-weight: 600;
    text-decoration: none;
    
    overflow: hidden;
    
    position: relative;
    
    
    &--bordered{
        border-color: $purple;        
        color: $black;
        
        transition: all .2s ease;
        
        &:hover{
            background: $purple;
            color: $white;
        }
    }
    
    &--purple{
        color: $purple; 
    }// end
    
    &--white{
        border-color: $white;        
        color: $white;
        
        transition: all .2s ease;
        
        &:hover{
            background: $white;
            color: $purple;
        }
    }
    
    &--filled-white{
        background: $white;
        color: $purple;
        
        transition: all .2s ease;
        
        &:hover{
            background: $purple;
            color: $white;
        }
    }
    
    &--filled-purple{
        background: $purple;
        color: $white;
        
        transition: all .2s ease;
        
        &:hover{
            background: $white;
            color: $purple;
            border-color: $purple;
        }
    }
    
}// end button



/**
* slide arrrows
*/

.arrow{
    display: block;
    width: 71px;
    height: 42px;
    border: none;
    margin: 0;
    padding: 0;
    
    appearance: none;
    
    background-image: url(../img/sprite/slide-arrow.png#controls);
    background-color: transparent;
    
    outline: none;
    cursor: pointer;
    
    position: absolute;
    z-index: 2;
    
    transition: all .2s ease;
    
    opacity: .5;
    
    &:hover{
        opacity: 1;
    }
    
    &--left{
        left: 20px;        
    }
    
    &--right{
        right: 20px;
        transform: rotate(180deg);
    }
    
}// arrow




/**
* content tiles 
*/

.tile{
    float: left;
    width: 50%;
    height: 342px;
    padding: 30px 60px 60px 60px;
    
    text-align: center;
    color: $white;
    text-decoration: none;  
    
    
    box-shadow: 2px 7px 49px rgba(27,27,30,.2);
    
    position: relative;
    
    &--flex{
        display: flex;
        align-items: center;
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }
    
    &--purple{
        background: $purple;
    }
    
    &--green{
        background: $green;
    }
    
    &--orange{
        background: $orange;
    }
    
    &--img{
        padding: 0;        
        overflow: hidden;
    }
    
    /**
    * tiles sizes
    */
    
    &--lg{
        height: 430px;
    }
    
    &--xs{
        height: 255px;
    }
    
    /**
    * styles for slide tile
    */
    
    &--transparent{
        padding: 0 50px 30px 0;
        
        box-shadow: none;
        
        text-align: left;
        position: relative;
        z-index: 1;
        
        &:after{
            content: '';
            display: block;
            width: 100%;
            height: 1000px;
            background: rgba($purple, .8);
            
            position: absolute;
            bottom: 0;
            left: 0;
            
            z-index: -1;
            
            transform: skewX(45deg);
            transform-origin: bottom;
        }
    }
    
    &__cathegory{
        margin: 15px 0;
        
        font-size: em(25);
        color: $purple--dark;
        text-shadow: 1px 2px 46px rgba(27,27,30,.75);
    }
    
    &__name{
        margin: 25px 0 35px 0;
        
        font-size: em(35);
        line-height: 1.4;
        font-weight: bold;
        text-transform: uppercase;
        letter-spacing: -1px;
        text-shadow: 1px 2px 46px rgba(27,27,30,.75);
    }
    
    > .button--filled-white{
        margin-left: 33px;
    }
    
    /**
    * styles for tiles text
    */
    &__ico{
        display: block;
        margin: 20px auto;
        
        z-index: 2;
    }
    
    &__title{
        margin: 30px 0;
        
        text-transform: uppercase;
        font-size: em(22);
        
        z-index: 2;
    }
    
    &__desc{
        margin: 5px 0;
        
        font-size: em(17);        
    }
    
    &__link{
        display: block;
        margin-bottom: 20px;
        
        font-size: em(22);
        text-decoration: underline;
        color: $white;
        
    }
    
    &__poster{
        width: 100%;
        height: 100%;
        
        &--bg{
            position: absolute;
            opacity: .3;
            z-index: 1;
        }
    }
    
}// end tile


/**
* slide big date
*/
.date{    
    color: $white;
    display: block;
    float: left;
    margin-top: -60px;
    
    display: flex;
    justify-content: flex-start;
    
    &__day{
        font-size: em(70);
        line-height: 1;
        font-weight: bold;
        float: left;
    } 
    
    &__month, &__year{
        font-size: em(25);
        font-weight: bold;
        line-height: 1.4;
        float: left;
    }
    
}// .date







/**
* tile icons
*/

.ico{
   
    &--discount{
        width: 50px;
        height: 34px;
        background: url(../img/sprite/discount-ico.png#controls);
    }
    
    &--tour{
        width: 83px;
        height: 55px;
        background: url(../img/sprite/d3tour-ico.png#controls);
    }
    
    &--tickets{
        width: 59px;
        height: 58px;
        background: url(../img/sprite/tickets-ico.png#controls);
    }
    
    &--cake{
        width: 61px;
        height: 58px;
        background: url(../img/sprite/happybirth-ico.png#controls);
    }
    
    &--science{
        width: 59px;
        height: 69px;
        background: url(../img/sprite/science-ico.png#controls);
    }
    
    &--coffee{
        width: 49px;
        height: 51px;
        background: url(../img/sprite/coffeecup-ico.png#controls);
    }
    
    &--store{
        width: 40px;
        height: 39px;
        background: url(../img/sprite/store-ico.png#controls);
    }
    
    &--neiron{
        width: 40px;
        height: 39px;
        background: url(../img/sprite/neiron-ico.png#controls);
    }
    
} // end ico



/**
* bullets navigation for slider
*/

.bullet{
    width: 100%;
    padding: 20px;
    
    position: absolute;
    bottom: 0;
    left: 0;
    
    &__item{
        display: inline-block;
        width: 14px;
        height: 14px;
        border-radius: 50%;
        margin: 0 3px;
        
        background: $white;
        
        cursor: pointer;
        
        transition: all .2s ease;
        
        &:hover{
            background: rgba($purple--dark, .5);
        }
    }
    
    
    
}// bullet



/**
* news slider styles
*/

.news{
    
    &__item{
        width: 33.333333%;
        float: left;
        height: 425px;
        
        overflow: hidden;
        
        position: relative;
        
        &:before{
            content: '';
            display: block;
            width: 100%;
            height: 100%;
            background: rgba($orange, .7);
            
            position: absolute;
            top: 0;
            left: 0;
        }
        
    }
    
    &__poster{
        display: block;
        float: left;
        width: 100%;
        height: auto;
    }
    
    &__caption{
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        display: table;
    }
    
    &__table{
        display: table-cell;
        vertical-align: middle;
        text-align: center;
        background: $white;
        
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        flex-direction: column;
        
        transition: all .5s ease;
        
    }
    
    &__date{
        margin-bottom: 40px;
        
        color: $purple;
        font-style: italic;
        font-size: em(18);
        
        transition: all .5s ease;
        
        position: relative;
        top: 0;
    }
    
    &__desc{
        transition: all .5s ease;
        position: relative;
        bottom: 0;
    }
    
    &__more{
        margin: 0 auto;
        display: none;
    }
    
}// news


.references{
    &__container{
        display: flex;
    }
}

/**
* Contacts green block
*/
.contacts{
    width: 40%;
    float: left;
    background: $green;
    padding: 65px 90px 80px 90px;
    
    color: $white;
    text-align: center;
    font-size: em(20);
    
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    
    &__txt{
        margin-bottom: 10px;
        &--caps{
            text-transform: uppercase;
        }
        
        &--bold{
            font-weight: bold;
        }
        
        &--heading{
            margin-bottom: 30px;
        }
    }
}

/**
* Small bottom tiles
*/
.tiles--sm{
    width: 60%;
    float: right;
}



/**
*
*/
.lecture{
    width: 100%;
    float: left;
    padding: 100px 0 100px 20%;
    
    &--inner{
        padding: 25px 50px 25px 100px;
        
        display: flex;
        align-items: center;
        
         &:nth-child(odd) {
            flex-direction: row-reverse;
        }
        
    }
    
    &__preview, &__desc{
        width: 50%;
        float: left;
    }
    
    &__preview{
        padding-right: 50px;
    }
    
    &__img{
        max-width: 425px;
        width: 100%;
        height: auto;
    }
    
    &__title{
        color: $text;
        text-transform: uppercase;
        font-size: em(22);
        margin: 40px 0;
    }
    
    &__description{
        font-size: em(14);
        color: $text;
        line-height: 1.5;
        margin-bottom: 40px;
    }
    
    &__panel{
        width: 50%;
        background: $purple;
        padding: 50px;
        box-shadow: 2px 3px 43px rgba(0,0,0,.35);
        color: $white;
    }
    
    &__thumbnail{
        width: 50%;
        height: 80%;
    }
    
    &__tag{
        font-size: em(18);
        margin-bottom: 12px;
    }
    
    &__name{
        font-size: em(25);
        text-transform: uppercase;
        font-weight: bold;
        text-shadow: 1px 2px 38px rgba(27,27,30,.75);
        margin: 25px 0 50px 0;
    }
    
    &__bottom{
        margin-top: 60px;
        .lecture__tag:last-child{
            margin-bottom: 0;
        }
    }
    
}// end lecture



/**
* Footer styles
*/
.footer{    
    background: $purple;
    padding: 70px 0 35px 0;
    
    color: $white;
    
    &__form-logo{
        width: 40%;
        float: left;
    }
    
    
    &__grid{
        width: 60%;
        float: left;
    }
    
    &__grid-column{
        width: 25%;
        float: left;
    }
    
    &__title{
        width: 100%;
        margin-bottom: 30px;
        float: left;
        
        color: $white;
        text-transform: uppercase;
        font-size: em(13);
        font-weight: bold;
    }
    
    &__link{
        margin-bottom: 10px;
        display: inline-block;
        
        color: $white;
        font-size: em(13);
        text-decoration: none;
        
        &:hover, &:focus{
            text-decoration: underline;
        }
    }
    
    &__list{
        display: block;
        width: 100%;
        float: left;
        margin-bottom: 35px;
    }
    
}// end footer


/**
* Class to add left purple line
*/
.ribbon--left{
    position: relative;
        &:before{
            content: '';
            display: block;
            height: 100%;
            width: 50%;
            position: absolute;
            left: 0;
            top: 0;
            background: $purple;
            z-index:-1;
        }
}

/**
* Class to add left purple line
*/
.ribbon--skew{
    position: relative;
        &:before{
            content: '';
            display: block;
            height: 100%;
            width: 50%;
            position: absolute;
            left: 0;
            top: 0;
            background: $purple;
            z-index:-1;
            
            transform: skewX(45deg);
            transform-origin: bottom;
        }
}


/**
* Class to add left purple line
*/
.ribbon--sm{
    position: relative;
        &:before{
            content: '';
            display: block;
            height: 100%;
            width: 30%;
            min-width: 470px;
            position: absolute;
            left: 0;
            top: 0;
            background: $purple;
            z-index:-1;
        }
}



/**
* Social links
*/
.social{
    display: block;
    float: left;
    width: 100%;
    
    &__item{
        display: inline-block;
        margin-right: 30px;
        &:last-of-type{
            margin-right: 0;
        }
    }
    
    &__link{
        &--fb{
            display: block;
            width: 10px;
            height: 22px;
            background: url(../img/sprite/facebook.png#controls);
            background-repeat: no-repeat;
            background-color: transparent;
        }
        
        &--tw{
            display: block;
            width: 25px;
            height: 21px;
            background: url(../img/sprite/twitter.png#controls);
            background-repeat: no-repeat;
            background-color: transparent;
        }
    }
}//end social


/**
* Breadcrumbs links
*/
.breadcrumb{
    padding: 48px 0;
    
    &__item{
        display: inline-block;
        float: left;
        
        &:last-of-type .breadcrumb__link{
            color: $white;
            display: block;
            margin-right: 0;
            &:before{
                content: '/ ';
            }
        }
    }
    
    &__back{
        width: 10px;
        height: 19px;
        display: block;
        margin-right: 35px;
        background: url(../img/sprite/breadcrumb-back.png#controls);
        background-repeat: no-repeat;
        background-color: transparent;
    }
    
    &__link{
        display: block;
        margin-right: 5px;
        line-height: 20px;
        color: $breadcrumb;
        text-transform: uppercase;
        text-decoration: none;
        font-size: em(15);
    }
    
}// end breadcrumb



/**
* Slide styles
*/
.carousel{
    display: block;
    width: 100%;
    float: left;
    overflow: hidden;
    min-height: 200px;    
    position: relative;
    
    &__item{       
        width: 100%;
        height: 100%; 
        position: absolute;
        left: 0;
        top: 0;
        
        opacity: 0;               
        visibility: hidden;
        transform: scale(0);
        
        transition: opacity .6s ease, transform .5s ease-in-out, visibility .5s linear;
        
    }
}


/**
* Scroll up link
*/
.scroll-up{
    display: block;
    width: 45px;
    height: 40px;
    
    position: fixed;
    bottom: - 100%;
    right:  -100%;
    z-index: 99;
    
    text-decoration: none;
    color: $text;
    text-align: center;
    
    cursor: pointer;
    transition: all .8s ease;
    
    &--shown{
        right: 15px;
        bottom: 20px;
    }
    
    &:hover::before{
        top: -10px;
    }
    
    &:before{
        content: '';
        display: inline-block;
        width: 10px;
        height: 19px;
        background: url(../img/sprite/breadcrumb-back.png#controls);
        background-repeat: no-repeat;
        background-color: transparent;
        position: relative;
        margin: 0 auto;
        top: 0;
        
        transition: all .2s ease;
        transform: rotate(90deg);
    }
    
    &:after{
        display: block;
        content: attr(data-text);
        text-transform: uppercase;        
        font-size: em(12);
    }
}