/**
* All general variables and functions
*/
/**
* Import styles as SMACSS convention
*/
html, body, div, span, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
abbr, address, cite, code,
del, dfn, em, img, ins, kbd, q, samp,
small, strong, sub, sup, var,
b, i,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, figcaption, figure,
footer, header, hgroup, menu, nav, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  font-weight: 100;
  vertical-align: baseline;
  background: transparent;
}

body {
  line-height: 1;
  font-family: sans-serif;
  font-size: 16px;
}

article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

ul {
  list-style: none;
}

a {
  margin: 0;
  padding: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
}

/* box model for all elements */
*, *:after, *:before {
  box-sizing: border-box;
}

/**
* Roboto font family
*/
@font-face {
  font-family: 'Roboto';
  src: url("../fonts/Roboto-Regular.woff2");
  src: url("../fonts/Roboto-Regular.woff") format("woff"), url("../fonts/Roboto-Regular.ttf") format("truetype");
  font-style: normal;
  font-weight: normal;
  text-rendering: optimizeLegibility;
}

@font-face {
  font-family: 'Roboto';
  src: url("../fonts/Roboto-Medium.woff2");
  src: url("../fonts/Roboto-Medium.woff") format("woff"), url("../fonts/Roboto-Medium.ttf") format("truetype");
  font-style: normal;
  font-weight: 600;
  text-rendering: optimizeLegibility;
}

body {
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
}

/**
* Main container
*/
[class$="__container"] {
  width: 100%;
  max-width: 1240px;
  margin: 0 auto;
  position: relative;
  padding-left: 20px;
  padding-right: 20px;
}

[class$="__container"]:before {
  content: '';
  display: table;
}

[class$="__container"]:after {
  content: '';
  display: table;
  clear: both;
}

/**
* Fluid container
*/
[class^="container--fluid"] {
  max-width: 1280px;
}

/**
* Row class
*/
[class$="--row"] {
  margin: 0 -20px;
}

/**
* Full width blocks
*/
.block {
  width: 100%;
  float: left;
}

body {
  min-width: 1240px;
}

/**
* Inner header margin bottom
*/
.header--inner {
  margin-bottom: 43px;
}

/**
* top search form
*/
.search {
  display: inline-block;
}

.search__input {
  width: auto;
  min-height: 15px;
  padding: 0 5px 5px 0;
  border: none;
  border-bottom: 1px solid #000;
  margin: 0 80px 0 15px;
  font-size: 0.9375em;
  background: none;
  outline: none;
  appearance: none;
}

.search__button {
  width: 15px;
  height: 14px;
  border: 0;
  margin: 0;
  padding: 5px;
  appearance: none;
  background: url(../img/sprite/m-glass.png#controls);
  background-repeat: no-repeat;
  background-color: transparent;
  outline: none;
  cursor: pointer;
}

/**
* Subscribe form
*/
.subscribe {
  width: 100%;
  float: left;
  display: block;
}

.subscribe__input {
  width: 250px;
  float: left;
  min-height: 15px;
  padding: 0 15px 5px 0;
  border: none;
  border-bottom: 1px solid #fff;
  margin: 0;
  font-size: 0.9375em;
  color: #fff;
  background: none;
  outline: none;
  appearance: none;
}

.subscribe__button {
  width: 13px;
  height: 12px;
  border: 0;
  margin: 0;
  padding: 5px;
  position: relative;
  left: -13px;
  appearance: none;
  background: url(../img/sprite/send.png#controls);
  background-repeat: no-repeat;
  background-color: transparent;
  outline: none;
  cursor: pointer;
}

/**
* language drop-down
*/
.lang {
  display: inline-block;
  padding: 0 20px 0 30px;
}

.lang__current {
  position: relative;
  text-transform: uppercase;
  font-size: 0.9375em;
  color: #424242;
  text-align: center;
  line-height: 1;
  cursor: pointer;
}

.lang__current:after {
  content: '';
  display: block;
  width: 11px;
  height: 6px;
  background: url(../img/sprite/carret.png#controls);
  position: absolute;
  top: 25%;
  right: -20px;
}

.lang__drop-down {
  height: 0;
  overflow: hidden;
}

/**
*  site logo and slug
*/
.logo {
  display: inline-block;
  text-decoration: none;
  color: #000;
  float: left;
  display: flex;
  align-items: center;
}

.logo__ico {
  display: block;
  width: 103px;
  height: 103px;
  background: url(../img/logo.png) no-repeat transparent;
  background-size: cover;
  float: left;
}

.logo__txt {
  display: block;
  float: left;
  padding-left: 20px;
}

.logo__name {
  display: block;
  margin-bottom: 5px;
  font-size: 1.4375em;
  font-weight: bold;
}

.logo__slug {
  display: block;
  font-size: 0.6875em;
}

.logo--bottom {
  color: #fff;
  margin-bottom: 60px;
}

.logo--bottom .logo__name {
  font-size: 1.0625em;
}

/**
* navigation links
*/
.top-nav {
  float: left;
  position: relative;
}

.top-nav__line {
  display: block;
  width: 100%;
  height: 3px;
  background: #fff;
  position: absolute;
  left: 0;
  bottom: 0;
}

.top-nav__marquee {
  display: block;
  height: 3px;
  background: #904eca;
  position: absolute;
  bottom: 0;
  width: 0;
  left: 0;
  transition: all .3s ease;
}

.top-nav__item {
  display: inline-block;
  float: left;
}

.top-nav > .top-nav__item:first-child .top-nav__link {
  padding-left: 20px;
}

.top-nav > .top-nav__item:last-of-type .top-nav__link {
  padding-right: 20px;
}

.top-nav__link {
  display: block;
  padding: 20px 10px;
  color: #000;
  text-decoration: none;
  text-transform: uppercase;
  text-align: center;
  font-size: 0.875em;
  font-weight: 600;
  transition: all .2s ease;
  background: #fff;
}

.top-nav__link:hover, .top-nav__link:focus {
  color: #904eca;
}

/**
* button styles 
*/
.button {
  display: inline-block;
  padding: 17px 30px;
  border: 3px solid transparent;
  text-align: center;
  text-transform: uppercase;
  line-height: 1;
  font-size: 0.875em;
  font-weight: 600;
  text-decoration: none;
  overflow: hidden;
  position: relative;
}

.button--bordered {
  border-color: #904eca;
  color: #000;
  transition: all .2s ease;
}

.button--bordered:hover {
  background: #904eca;
  color: #fff;
}

.button--purple {
  color: #904eca;
}

.button--white {
  border-color: #fff;
  color: #fff;
  transition: all .2s ease;
}

.button--white:hover {
  background: #fff;
  color: #904eca;
}

.button--filled-white {
  background: #fff;
  color: #904eca;
  transition: all .2s ease;
}

.button--filled-white:hover {
  background: #904eca;
  color: #fff;
}

.button--filled-purple {
  background: #904eca;
  color: #fff;
  transition: all .2s ease;
}

.button--filled-purple:hover {
  background: #fff;
  color: #904eca;
  border-color: #904eca;
}

/**
* slide arrrows
*/
.arrow {
  display: block;
  width: 71px;
  height: 42px;
  border: none;
  margin: 0;
  padding: 0;
  appearance: none;
  background-image: url(../img/sprite/slide-arrow.png#controls);
  background-color: transparent;
  outline: none;
  cursor: pointer;
  position: absolute;
  z-index: 2;
  transition: all .2s ease;
  opacity: .5;
}

.arrow:hover {
  opacity: 1;
}

.arrow--left {
  left: 20px;
}

.arrow--right {
  right: 20px;
  transform: rotate(180deg);
}

/**
* content tiles 
*/
