  width: 50%;
  height: 342px;
  padding: 30px 60px 60px 60px;
  text-align: center;
  color: #fff;
  text-decoration: none;
  box-shadow: 2px 7px 49px rgba(27, 27, 30, 0.2);
  position: relative;
  /**
    * tiles sizes
    */
  /**
    * styles for slide tile
    */
  /**
    * styles for tiles text
    */
}

.tile--flex {
  display: flex;
  align-items: center;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.tile--purple {
  background: #904eca;
}

.tile--green {
  background: #90c004;
}

.tile--orange {
  background: #dd892d;
}

.tile--img {
  padding: 0;
  overflow: hidden;
}

.tile--lg {
  height: 430px;
}

.tile--xs {
  height: 255px;
}

.tile--transparent {
  padding: 0 50px 30px 0;
  box-shadow: none;
  text-align: left;
  position: relative;
  z-index: 1;
}

.tile--transparent:after {
  content: '';
  display: block;
  width: 100%;
  height: 1000px;
  background: rgba(144, 78, 202, 0.8);
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: -1;
  transform: skewX(45deg);
  transform-origin: bottom;
}

.tile__cathegory {
  margin: 15px 0;
  font-size: 1.5625em;
  color: #3d176b;
  text-shadow: 1px 2px 46px rgba(27, 27, 30, 0.75);
}

.tile__name {
  margin: 25px 0 35px 0;
  font-size: 2.1875em;
  line-height: 1.4;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: -1px;
  text-shadow: 1px 2px 46px rgba(27, 27, 30, 0.75);
}

.tile > .button--filled-white {
  margin-left: 33px;
}

.tile__ico {
  display: block;
  margin: 20px auto;
  z-index: 2;
}

.tile__title {
  margin: 30px 0;
