  text-decoration: underline;
  color: #fff;
}

.tile__poster {
  width: 100%;
  height: 100%;
}

.tile__poster--bg {
  position: absolute;
  opacity: .3;
  z-index: 1;
}

/**
* slide big date
*/
.date {
  color: #fff;
  display: block;
  float: left;
  margin-top: -60px;
  display: flex;
  justify-content: flex-start;
}

.date__day {
  font-size: 4.375em;
  line-height: 1;
  font-weight: bold;
  float: left;
}

.date__month, .date__year {
  font-size: 1.5625em;
  font-weight: bold;
