}

.ico--tickets {
  width: 59px;
  height: 58px;
  background: url(../img/sprite/tickets-ico.png#controls);
}

.ico--cake {
  width: 61px;
  height: 58px;
  background: url(../img/sprite/happybirth-ico.png#controls);
}

.ico--science {
  width: 59px;
  height: 69px;
  background: url(../img/sprite/science-ico.png#controls);
}

.ico--coffee {
  width: 49px;
  height: 51px;
  background: url(../img/sprite/coffeecup-ico.png#controls);
}

.ico--store {
  width: 40px;
  height: 39px;
