}

/**
* bullets navigation for slider
*/
.bullet {
  width: 100%;
  padding: 20px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.bullet__item {
  display: inline-block;
  width: 14px;
  height: 14px;
  border-radius: 50%;
  margin: 0 3px;
  background: #fff;
  cursor: pointer;
  transition: all .2s ease;
}

.bullet__item:hover {
  background: rgba(61, 23, 107, 0.5);
}

/**
* news slider styles
*/
.news__item {
  width: 33.333333%;
  float: left;
  height: 425px;
  overflow: hidden;
  position: relative;
}

.news__item:before {
  content: '';
  display: block;
  width: 100%;
  height: 100%;
  background: rgba(221, 137, 45, 0.7);
  position: absolute;
  top: 0;
  left: 0;
}

.news__poster {
  display: block;
  float: left;
  width: 100%;
  height: auto;
}

.news__caption {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  display: table;
}

.news__table {
  display: table-cell;
  vertical-align: middle;
  text-align: center;
  background: #fff;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  transition: all .5s ease;
}

.news__date {
  margin-bottom: 40px;
  color: #904eca;
  font-style: italic;
  font-size: 1.125em;
  transition: all .5s ease;
  position: relative;
  top: 0;
}

.news__desc {
  transition: all .5s ease;
  position: relative;
  bottom: 0;
}

.news__more {
  margin: 0 auto;
  display: none;
}

.references__container {
  display: flex;
}

/**
* Contacts green block
*/
.contacts {
  width: 40%;
  float: left;
  background: #90c004;
  padding: 65px 90px 80px 90px;
  color: #fff;
  text-align: center;
  font-size: 1.25em;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.contacts__txt {
  margin-bottom: 10px;
}

.contacts__txt--caps {
  text-transform: uppercase;
}

.contacts__txt--bold {
  font-weight: bold;
}

.contacts__txt--heading {
  margin-bottom: 30px;
}

/**
* Small bottom tiles
*/
.tiles--sm {
  width: 60%;
  float: right;
}

/**
*
*/
.lecture {
  width: 100%;
  float: left;
  padding: 100px 0 100px 20%;
}

.lecture--inner {
  padding: 25px 50px 25px 100px;
  display: flex;
  align-items: center;
}

.lecture--inner:nth-child(odd) {
  flex-direction: row-reverse;
}

.lecture__preview, .lecture__desc {
  width: 50%;
  float: left;
}

.lecture__preview {
  padding-right: 50px;
}

.lecture__img {
  max-width: 425px;
  width: 100%;
  height: auto;
}

.lecture__title {
  color: #282b30;
  text-transform: uppercase;
  font-size: 1.375em;
  margin: 40px 0;
}

.lecture__description {
  font-size: 0.875em;
  color: #282b30;
  line-height: 1.5;
  margin-bottom: 40px;
}

.lecture__panel {
  width: 50%;
  background: #904eca;
  padding: 50px;
  box-shadow: 2px 3px 43px rgba(0, 0, 0, 0.35);
  color: #fff;
}

.lecture__thumbnail {
  width: 50%;
  height: 80%;
}

.lecture__tag {
  font-size: 1.125em;
  margin-bottom: 12px;
}

.lecture__name {
  font-size: 1.5625em;
  text-transform: uppercase;
  font-weight: bold;
  text-shadow: 1px 2px 38px rgba(27, 27, 30, 0.75);
  margin: 25px 0 50px 0;
}

.lecture__bottom {
  margin-top: 60px;
}

.lecture__bottom .lecture__tag:last-child {
  margin-bottom: 0;
}

/**
* Footer styles
*/
.footer {
  background: #904eca;
  padding: 70px 0 35px 0;
  color: #fff;
}

.footer__form-logo {
  width: 40%;
  float: left;
}

.footer__grid {
  width: 60%;
  float: left;
}

.footer__grid-column {
  width: 25%;
  float: left;
}

.footer__title {
  width: 100%;
  margin-bottom: 30px;
  float: left;
  color: #fff;
  text-transform: uppercase;
  font-size: 0.8125em;
  font-weight: bold;
}

.footer__link {
  margin-bottom: 10px;
  display: inline-block;
  color: #fff;
  font-size: 0.8125em;
  text-decoration: none;
}

.footer__link:hover, .footer__link:focus {
  text-decoration: underline;
}

.footer__list {
  display: block;
  width: 100%;
  float: left;
  margin-bottom: 35px;
}

/**
* Class to add left purple line
*/
.ribbon--left {
  position: relative;
}

.ribbon--left:before {
  content: '';
  display: block;
  height: 100%;
  width: 50%;
  position: absolute;
  left: 0;
  top: 0;
  background: #904eca;
  z-index: -1;
}

/**
* Class to add left purple line
*/
.ribbon--skew {
  position: relative;
}

.ribbon--skew:before {
  content: '';
  display: block;
  height: 100%;
  width: 50%;
  position: absolute;
  left: 0;
  top: 0;
  background: #904eca;
  z-index: -1;
  transform: skewX(45deg);
  transform-origin: bottom;
}

/**
* Class to add left purple line
*/
.ribbon--sm {
  position: relative;
}

.ribbon--sm:before {
  content: '';
  display: block;
  height: 100%;
  width: 30%;
  min-width: 470px;
  position: absolute;
  left: 0;
  top: 0;
  background: #904eca;
  z-index: -1;
}

/**
* Social links
*/
.social {
  display: block;
  float: left;
  width: 100%;
}

.social__item {
  display: inline-block;
  margin-right: 30px;
}

.social__item:last-of-type {
  margin-right: 0;
}

.social__link--fb {
  display: block;
  width: 10px;
  height: 22px;
  background: url(../img/sprite/facebook.png#controls);
  background-repeat: no-repeat;
  background-color: transparent;
}

.social__link--tw {
  display: block;
  width: 25px;
  height: 21px;
  background: url(../img/sprite/twitter.png#controls);
  background-repeat: no-repeat;
  background-color: transparent;
}

/**
* Breadcrumbs links
*/
.breadcrumb {
  padding: 48px 0;
}

.breadcrumb__item {
  display: inline-block;
  float: left;
}

.breadcrumb__item:last-of-type .breadcrumb__link {
  color: #fff;
  display: block;
  margin-right: 0;
}

.breadcrumb__item:last-of-type .breadcrumb__link:before {
  content: '/ ';
}

.breadcrumb__back {
  width: 10px;
  height: 19px;
  display: block;
  margin-right: 35px;
  background: url(../img/sprite/breadcrumb-back.png#controls);
  background-repeat: no-repeat;
  background-color: transparent;
}

.breadcrumb__link {
  display: block;
  margin-right: 5px;
  line-height: 20px;
  color: #afaab4;
  text-transform: uppercase;
  text-decoration: none;
  font-size: 0.9375em;
}

/**
* Slide styles
*/
.carousel {
  display: block;
  width: 100%;
  float: left;
  overflow: hidden;
  min-height: 200px;
  position: relative;
}

.carousel__item {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  opacity: 0;
  visibility: hidden;
  transform: scale(0);
  transition: opacity .6s ease, transform .5s ease-in-out, visibility .5s linear;
}

/**
* Scroll up link
*/
.scroll-up {
  display: block;
  width: 45px;
  height: 40px;
  position: fixed;
  bottom: -100%;
  right: -100%;
  z-index: 99;
  text-decoration: none;
  color: #282b30;
  text-align: center;
  cursor: pointer;
  transition: all .8s ease;
}

.scroll-up--shown {
  right: 15px;
  bottom: 20px;
}

.scroll-up:hover::before {
  top: -10px;
}

.scroll-up:before {
  content: '';
  display: inline-block;
  width: 10px;
  height: 19px;
  background: url(../img/sprite/breadcrumb-back.png#controls);
  background-repeat: no-repeat;
  background-color: transparent;
  position: relative;
  margin: 0 auto;
  top: 0;
  transition: all .2s ease;
  transform: rotate(90deg);
}

.scroll-up:after {
  display: block;
  content: attr(data-text);
  text-transform: uppercase;
  font-size: 0.75em;
}

/**
* Container for top language drop down and search form
*/
.lang-seach {
  padding: 25px 0;
  text-align: right;
  display: flex;
  align-content: center;
  align-items: center;
  justify-content: flex-end;
}

/**
*  Order tickets button on top
*/
.tickets {
  float: right;
  display: inline-block;
}

/**
* Container for site navigation
*/
.menu {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

/**
* Display inline navigation links
*/
.main-nav {
  display: inline-block;
}

/**
* Top space for beatween navigation and tiles
*/
.slide__container {
  padding-top: 375px;
}

/**
* Positioning slide control arrows 
*/
.slide .arrow {
  top: 220px;
}

/**
* Positioning news feed arows
*/
.news .arrow {
  bottom: 35px;
}

/**
* Positioning button to right
*/
.lecture .button:last-of-type {
  float: right;
}

.lecture--inner .button:last-of-type {
  float: none;
}

/**
* Additional purple background for ellements
*/
.references {
  position: relative;
}

.references:after {
  content: '';
  display: block;
  width: 100%;
  height: 200px;
  position: absolute;
  bottom: 0;
  right: 0;
  background: #904eca;
  z-index: -1;
}

.footer .subscribe {
  margin-bottom: 60px;
}

.main-bg {
  position: relative;
  width: 100%;
  float: left;
}

.main-bg:after {
  content: '';
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: url(../img/main-bg.jpg) no-repeat transparent top center;
  z-index: -1;
}

.bullet__item--active {
  background: #3d176b;
}

.carousel__item--active {
  opacity: 1;
  visibility: visible;
  transform: scale(1);
}

.arrow--inactive {
  opacity: .4;
}

.arrow--inactive:hover {
  opacity: .4;
}

.news__item:hover .news__table {
  background: transparent;
}

.news__item:hover .news__desc {
  bottom: -100%;
}

.news__item:hover .news__date {
  top: -100%;
}

.news__item:hover .news__more {
  display: block;
}

