.main-bg{
    position: relative;
    width: 100%;
    float: left;
    &:after{
        content: '';
        display: block;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        background: url(../img/main-bg.jpg) no-repeat transparent top center;
        z-index: -1;
    }
}



.bullet{
    &__item--active{
        background: $purple--dark;
    }
}


.carousel{
    &__item--active{
        opacity: 1;
        visibility: visible;
        transform: scale(1);
    }
}


.arrow{
    &--inactive{
        opacity: .4;
        &:hover{
            opacity: .4;
        }
    }
}



.news__item:hover{
     .news__table{
         background: transparent;
     }
     
     .news__desc{
         bottom: -100%;
     }
        
     .news__date{
         top: -100%;
     }
        
     .news__more{
         display: block;
     }
    
}// news